import { useState, useEffect } from "react";
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";
import MKBox from "components/MKBox";
import MKTypography from "components/MKTypography";
import DefaultNavbar from "examples/Navbars/DefaultNavbar";
import SimpleFooter from "examples/Footers/SimpleFooter";
import 'style/css.css'
import routes from "routes";
import axios from "axios";
import Helper from "helper";
import MKAvatar from "components/MKAvatar";
import { useNavigate } from "react-router-dom";

function RankPage() {
  const [noAuth, setNoAuth] = useState(false);
  const [rankArr, setRankArr] = useState(null);
  const navigate = useNavigate();
  let rankIndex = 1;

  useEffect(async () => {
    try {
      const url = `${Helper.host}/restAPI/rankController.php?action=getRank`;
      const res = await axios.get(url, Helper.hostHeaders);
      console.log(res.data.result)
      if (res.data.result) {
        setRankArr(res.data.result);
      } else {
        throw new Error(res.data.message);
      }
    } catch (e) {
      if (e.response.data.code == 403) {
        setNoAuth(true);
      }
      setRankArr(null)
    }
  }, [])


  return (
    <>
      <DefaultNavbar
        routes={routes}
        action={false}
        sticky
      />
      <MKBox component="section" pt={20} pb={12}>
        <Container>
          <Grid container justifyContent="center">
            <Grid item xs={12}>
              <Card>
                <MKBox
                  variant="gradient"
                  bgColor="info"
                  borderRadius="lg"
                  coloredShadow="info"
                  p={3}
                  mt={-3}
                  mx={2}
                >
                  <MKTypography variant="h3" color="white">
                    差点排名
                  </MKTypography>
                  <MKTypography variant="body2" color="white" opacity={0.8}>
                    最高统计最近20场比赛成绩，再取其中一半最好成绩进行计算。
                  </MKTypography>
                </MKBox>
                <MKBox p={{ xs: 3, md: 6 }}>
                  <MKTypography variant="body2" color="grey" opacity={0.8} hidden={!noAuth}>
                    您无权查看本页面
                  </MKTypography>
                  <div className="table_1_wrap">
                    <table className="table_1" hidden={rankArr == null}>
                      <thead>
                        <tr>
                          <th>排名</th>
                          <th>头像</th>
                          <th>姓名</th>
                          <th>差点指数</th>
                          <th>统计场数</th>
                        </tr>
                      </thead>
                      <tbody>
                        {
                          rankArr && rankArr.map(x => {
                            return (
                              <tr key={rankIndex}>
                                <td>{rankIndex++}</td>
                                <td><MKAvatar src={`${Helper.host}${x.user_avatar}`} alt="Burce Mars" size="s" shadow="xl" /></td>
                                <td>{x.user_first_name} {x.user_last_name}</td>
                                <td><a className="a1" onClick={() => navigate(`/rank/detail/${x.user_id}`)}>{x.rank_handicap_index}</a></td>
                                <td>{x.rank_history_count}</td>
                              </tr>
                            )
                          })
                        }
                      </tbody>
                    </table>
                  </div>
                </MKBox>
              </Card>
            </Grid>
          </Grid>
        </Container>
      </MKBox>
      <MKBox pt={{ xs: 0, lg: 3 }} pb={3}>
        <SimpleFooter />
      </MKBox>
    </>
  );
}

export default RankPage;
